import uniNavBar from '../../components/uni-nav-bar/uni-nav-bar.vue';
import uploadImage from '../../components/common/upload-image.vue';
import uniPopup from '../../components/uni-popup/uni-popup.vue';
export default {
	data() {
		return {
			visibles: ['所有人可见', '尽自己可见'],
			visible: '所有人可见',
			content: '',
			imageList: [],
			showPopup: true,
			isFirstBack: true
		};
	},
	components: {
		uniNavBar,
		uploadImage,
		uniPopup
	},
	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		release() {
			console.log('发布');
		},
		changeLook() {
			uni.showActionSheet({
				itemList: this.visibles,
				success: res => {
					this.visible = this.visibles[res.tapIndex];
				}
			});
		},
		lookup() {
			console.log(this.imageList.length);
		},
		updateImage(sonImageList) {
			this.imageList = sonImageList;
		},
		open() {
			this.$refs.showRules.open();
		},
		closePopup() {
			this.$refs.showRules.close();
		}
	},
	onReady() {
		this.$refs.showRules.open();
	},
	onBackPress() {
		if (this.isFirstBack) {
			if (this.content.length > 0 || this.imageList.length > 0) {
				uni.showModal({
					content: '是否要保存草稿',
					confirmText: '保存',
					cancelText: '不保存',
					success: res => {
						if (res.confirm) {
							console.log('保存草稿');
						} else {
							console.log('不保存草稿');
						}
						this.isFirstBack = false;
						uni.navigateBack({
							delta: 1
						});
					}
				});
				return true;
			}
		}
	}
};
